import { useMemo } from 'react'
import { useTheme } from '~/hooks/use-theme'

const FlyingShit = () => {
    const { theme, toggleTheme } = useTheme()
    const isDarkMode = useMemo(() => theme === 'dark', [theme])

    return isDarkMode ? (
        <img src='../../../assets/Alirom_Logo_Variations-05.png' width={200} style={{ position: 'absolute' }} />
    ) : (
        <img src='../../../assets/Alirom_Logo_Variations-06.png' width={200} style={{ position: 'absolute' }} />
    )
}

export default FlyingShit

